import React from "react";

// const Button = ({value, color, onClick}) => {
const Button = ({value, color, onClick}) => {

    return (
      <div
       onClick={()=>onClick()}
        style={{
            padding: 5,
            width: 60,
            margin: 5,
            backgroundColor: color,
            fontSize: "1rem",
            textAlign: "center",
            borderRadius: "10%",
            marginRight: "auto",
            marginLeft: "auto",
            fontWeight: "600",
            textTransform: "uppercase"
        }}>
            {value}

      </div>  
    );

    
};

export default Button;